@font-face {
    font-family: 'Raleway Regular';
    src: local('Raleway Regular'),
    local('Raleway-Regular'),
    url(./fonts/raleway/Raleway-Regular.ttf) format('truetype'),
    url(./fonts/raleway/Raleway-Regular.woff) format('woff');
}

body {
    margin: 0;
    font-family: "Raleway-Regular", serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.orderBlockCustomScrollBar {
    height: 587px;
    overflow: auto;
    scrollbar-width: thin;
    scrollbar-color: #720D5D #fff;
    display: flex;
    padding: 1% 2%;
    position: relative;
    top: 1px;
    justify-content: space-evenly
}

.drawer_nav_selected {
    background-color:  rgba(196, 196, 196, 0.2);
    border-left-color: #EF3F34!important;
    color: #EF3F34!important;
}

.orderBlockCustomScrollBarMobileShow {
    height: 587px;
    overflow: auto;
    scrollbar-width: thin;
    scrollbar-color: #720D5D #fff;
    display: flex;
    padding: 1% 2%;
    justify-content: flex-start;
    position: absolute;
    bottom: -35.9%;
    z-index: 1;
    background: #fff;
    flex-direction: column;
    width: 101vw;
    left: -4px;
}

.orderBlockCustomScrollBarMobileShow.orderBlockCustomScrollBarMobileShowMaterials {
    bottom: 130px;
    height: 184px;
}

div[aria-describedby="simple-modal-description"] {
    display: flex;
    justify-content: center;
    align-items: center;
}

.capital_first_letter:first-letter {
    text-transform:capitalize;
}

/*спиннер начало*/
.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 22px;
}

.fullWidthFullHeightCenter {
    width: 100%;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.lds-ellipsis div {
    position: absolute;
    top: 4px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #fff;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.lds-ellipsis-purple  div {
    background: #d291c1;
}


.lds-ellipsis div:nth-child(1) {
    left: 8px;
    animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
    left: 8px;
    animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
    left: 32px;
    animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
    left: 56px;
    animation: lds-ellipsis3 0.6s infinite;
}

.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
    border-color: #5D1049!important;
}


@keyframes lds-ellipsis1 {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes lds-ellipsis3 {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}
@keyframes lds-ellipsis2 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(24px, 0);
    }
}

/*спиннер конец*/

.customSelect .MuiOutlinedInput-input {
    padding: 5px;
}



.orderBlockCustomScrollBar::-webkit-scrollbar {
    width: 13px; /* width of the entire scrollbar */
}

.orderBlockCustomScrollBar::-webkit-scrollbar-track {
    background: #fafafa; /* color of the tracking area */
}

.orderBlockCustomScrollBar::-webkit-scrollbar-thumb {
    background-color: #720D5D; /* color of the scroll thumb */
    border-radius: 20px; /* roundness of the scroll thumb */
    border: 3px solid #fff; /* creates padding around scroll thumb */
    height: 50px;
}

@media (max-width: 600px) {
    /* xs у нас */
    .orderBlockCustomScrollBar {
        flex-direction: column;
        justify-content: initial;
    }

    .orderBlockCustomScrollBarMobileShow {
        overflow-x: hidden;
    }
}
