.arrowBlock {
    position: relative;
}
.arrowIcon {
    background-color: transparent;
    height: 20px;
    width: 49px;
    display: block;
    padding: 0.5em;
    position: relative;
    cursor: pointer;
    border-radius: 4px;
    overflow: hidden;
}
.leftBar {
    position: absolute;
    background-color: transparent;
    top: 0;
    left: 0;
    width: 40px;
    height: 10px;
    display: block;
    transform: rotate(35deg);
    float: right;
    border-radius: 2px;
}
.leftBar:after {
    content: "";
    background-color: black;
    width: 10px;
    height: 3px;
    display: block;
    float: right;
    border-radius: 6px 10px 10px 6px;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 0.8);
    z-index: -1;
}
.rightBar {
    position: absolute;
    background-color: transparent;
    top: 7px;
    left: 41px;
    width: 3px;
    height: 1px;
    display: block;
    transform: rotate(-35deg);
    float: right;
    border-radius: 2px;
}
.rightBar:after {
    content: "";
    background-color: black;
    width: 10px;
    height: 3px;
    display: block;
    float: right;
    border-radius: 10px 6px 6px 10px;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 0.8);
    z-index: -1;
}

.shadowBlock {
    width: 30px;
    height: 29px;
    border-radius: 50%;
    background: #e2dede;
    position: absolute;
    top: -4px;
    right: -3px;
    display: none;
}

.arrowBlock:hover .shadowBlock {
    display: block;
}

.leftBarOpen:after {
    transform-origin: center center;
    transform: rotate(-70deg);
}
.rightBarOpen:after {
    transform-origin: center center;
    transform: rotate(70deg);
}

/*Click Effect*/


.stuff {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.button {
    position: relative;
    display: inline-block;
    cursor: pointer;
    margin: 0 auto;
    width: 70px;
    height: 70px;
    font-size: 52px;
    transition: all 0.4s ease-in;
}

.button span[class^='ion'] {
    position: relative;
}
.button:before {
    content: '';
    background-color: aliceblue;
    border-radius: 50%;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: scale(0.001,0.001);
}
.button:focus {
    outline: 0;
    color: #fff;
}
.button:focus:before {
    animation: effect_dylan 0.8s ease-out;
}
@keyframes effect_dylan {
    50% {
        transform: scale(1.5,1.5);
        opacity: 0;
    }
    99% {
        transform: scale(0.001,0.001);
        opacity: 0;
    }
    100% {
        transform: scale(0.001,0.001);
        opacity: 1;
    }
}
